.Intro {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.Intro__card {
  display: flex;
  flex-direction: column;
  margin: 10% auto;
  width: 65vw;
  height: 60vh;
  border-radius: 40px;
  background-color: #483f72;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 7px rgba(0, 0, 0, 0.22);
  transition: 0.2s;
  color: white;
  z-index: 9;
}

.Intro__profilepic {
  width: 200px;
  height: 200px;
  margin-top: 2px;
  align-self: center;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 7px rgba(0, 0, 0, 0.22);

  display: inline-block;
  vertical-align: middle;
  border: 1px solid #03bfcb;
  border-radius: 50%;
  padding: 7px;
}

.Intro__Skills {
  margin-top: 180px;
}

.Intro__infoicon {
  padding: 8px;
  cursor: pointer;
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity 0.4s ease;
}

.Intro__written {
  display: flex;
  justify-content: center;
  margin: 2.5rem;
}

h1 {
  color: rgb(255, 255, 255);
  font-family: "Roboto Condensed", sans-serif;
  font-size: 36px;
  font-weight: 700;
  text-align: center;
  top: -1%;
  position: relative;
}

